.communityContentBox{
    width:70%;   
    margin:100px auto;
}


.formBox{
    border-top:1px solid black;
    padding-top:80px;
    text-align: left;
    margin-bottom:50px;
    border-bottom: 1px solid grey; 
}


.formBox label{
    font-size:20pt;
}



.formBox input{
    width:65%;
    height:30px;
}

#contentSubject{
    font-size:12pt;
}

#contentTextBox{
    width:100%;
    height:400px;
    font-size:12pt;
}



.submit{
    display: flex;
    justify-content: flex-end;
    margin-bottom:20px;
}

.btnSubmit{
    width:50px;
    height:40px;
    margin-left:20px;
    font-size:10pt;
    border-radius: 3px;
    font-weight: bold;
}
#btnFile{
    width:70px;
}

.btnWhite{
    background-color: white;
    color:blue;
    border : 1px solid blue;
}





/* 게시글 - communityMain */
.양식{
    background-color: lightgray;
}

.게시글{
    border-bottom : 1px solid black;
    height:50px;
    display:grid;
    align-items: center;
    grid-template-columns: 1fr 4fr 1fr; /* 3개의 열을 가진 그리드 */
    grid-auto-flow: row; 
}